import { NgModule } from '@angular/core';
import { Routes, RouterModule, } from '@angular/router';
import { AdminComponent } from './admin/admin.component';
import { GetroomuserlistComponent } from './getroomuserlist/getroomuserlist.component';
import { GetunlocklistComponent } from './getunlocklist/getunlocklist.component';
import { AdminloginComponent } from './adminlogin/adminlogin.component';
import { EdithtmlComponent } from './edithtml/edithtml.component';


const routes: Routes = [
    // {path: '', pathMatch: 'full', redirectTo: ''},
    {
        path: '', component: AdminComponent,
        children: [
            { path: '', pathMatch: 'full', redirectTo: 'getroomuserlist' },
            { path: 'getroomuserlist', component: GetroomuserlistComponent },
            { path: 'getunlocklist', component: GetunlocklistComponent },
            //{ path: 'admin', component: AdminloginComponent },
            { path: 'edit', component: EdithtmlComponent },
        ]
    },
    //{ path: 'adminlogin', component: AdminloginComponent },
];
@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class AdminRoutingModule { }